.card {
    margin: 1rem 1.5rem 1.5rem;
    width: 30rem;
    min-width: 20rem;
    height: 40rem;
    background-color: #ffffff;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    border-radius: .5rem;
    box-shadow: 0px 6px 18px -9px rgba( 0, 0, 0, 1);
    transition: transform 100ms ease-in;
    cursor: pointer;
}

.card:hover {
    transform: scale(1.02);
}

.card__img {
    object-fit: cover;
    height: 65%;
    width: 100%;
    border-radius: .5rem .5rem 0rem 0rem;
}

.card__info {
    display: flex;
    height: 35%;
    flex-direction: column;
    justify-content: center;
    padding: 1rem .75rem 2rem;
}

.card__content {
    padding: .5rem;
}

.card__price {
    font-weight: 900;
}

@media(max-width: 47rem){

    .card {
        margin: 1rem 1rem 1.5rem;
        width: 20rem;
        min-width: 18rem;
        height: 35rem;
    }

    .card__img {
        height: 60%;
    }

    .card__info {
        height: 40%;
    }

    .card__content {
        font-size: 1rem;
        padding: .25rem;
    }

    .card__title {
        font-size: 1.25rem;
    }
}